
.listWrapper{
  box-sizing: border-box;
  background-color: linen;
  margin: 0;
  padding: 0 calc(30px) 0;
}

.item{
  border: calc(2px) solid #8900F2;
  border-radius: calc(8px);
  box-sizing: border-box;
  padding: calc(14px);
  position: relative;
}

.item + .item{
  margin-top: calc(20px)
}

.taskName {
  font-size: calc(26px);
  padding: calc(8px) calc(12px);
  border-radius: calc(6px);
  line-height: 100%;
}

.taskNameId {
  font-size: calc(20px);
  line-height: 100%;
  display: block;
  margin-bottom: calc(12px);
}

.taskBtn {
  font-size: calc(28px);
  line-height: 100%;
  height: auto;
  padding: calc(8px) calc(14px);
  box-sizing: border-box;
}

@media(max-width: 1020px){
  .listWrapper {
    max-width: unset;
    min-width: auto;
    padding: 0 30px 0;
  }
  .item + .item{
    margin-top: 20px;
  }
  .item {
    padding: 14px;
    border: 2px solid #8900F2;
    border-radius: 8px;
  }
  .item button + button { 
    margin-top: 8px;
  }
  .taskName {
    font-size: 22px;
    border-radius: 6px;
  }
  .taskNameId { 
    font-size: 18px;
    margin-bottom: 10px;
  } 
  .taskName span{
    display: block;
  }
  .taskBtn {
    font-size: 20px;
    padding: 8px 12px;
  }
  .taskBtn:first-child{
    margin-bottom: 8px;
  }
}

