
.svgWrapper{
  position: relative;
  margin: 0 auto;
  text-align: center;
  max-width: max-content;
}

.svgWrapper svg{ 
  width: calc(1000px);
}

svg path:hover {
  fill: red !important;
  transition: 0.5s;
  cursor: pointer;
}

@media(max-width: 1368px){
  .svgWrapper svg{
    width: calc(700px);
  }
}
@media(max-width: 1279px){
  .svgWrapper {
    display: flex;
    flex-direction: column;
  }
}
@media(max-width: 768px){
  .svgWrapper svg{ 
    width: 340px;
    height: 340px;
  }
}