.elWrapper {
  box-sizing: border-box;
  padding: calc(14px);
  position: absolute;
  text-align: center;
  width: calc(220px);
  min-height: calc(300px);
  background-color: #F5E3F7;
  z-index: 100;
  bottom: calc(100px);
  right: calc(-220px);
  border-radius: calc(6px);
}

.elSubTitle {
  margin-top: calc(20px);
  font-size: calc(18px);
  line-height: 100%;
  display: block;
}

@media(max-width: 1279px) {
  .elWrapper {
    position: unset;
    width: 100%;
    order: 2;
    min-height: unset;
    border-radius: 6px;
    padding: 14px;
  }
}